<template>
  <div class="energyTrendAnalysis">
    <div class="left border-box tree">
      <el-tree
        :default-expand-all="true"
        :expand-on-click-node="false"
        :data="data"
        :props="defaultProps"
        @node-click="nodeClick"
      ></el-tree>
    </div>
    <div class="right border-box">
      <div class="search chartSearch">
        <div>
          <el-date-picker
            v-model="date1"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="pvAlarmQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
        <div style="color: #00b4fe">至</div>
        <div>
          <el-date-picker
            v-model="date2"
            type="date"
            placeholder=""
            value-format="yyyy-MM-dd"
            @change="pvAlarmQueryFun"
            :clearable="false"
          >
          </el-date-picker>
        </div>
      </div>
      <div class="tabel">
        <el-table :data="tableData" style="width: 100%" height="100%">
          <el-table-column prop="pVStationId" label="电站名称" width="150">
          </el-table-column>
          <el-table-column prop="alarmId" label="告警编号" width="130">
          </el-table-column>
          <el-table-column prop="alarmType" label="告警类型" width="120">
          </el-table-column>
          <el-table-column prop="alarmNum" label="告警设备总数" width="120">
          </el-table-column>
          <el-table-column prop="alarmDate" label="告警日期"> </el-table-column>
          <el-table-column prop="alarmTime" label="告警时间"> </el-table-column>
          <el-table-column prop="timeLong" label="处理时长"> </el-table-column>
          <el-table-column prop="alarmStatus" label="告警状态">
          </el-table-column>
          <el-table-column label="操作" width="100">
            <template slot-scope="scope">
              <el-button
                type="text"
                size="small"
                @click="handleClick(scope.row)"
                >详情</el-button
              >
            </template>
          </el-table-column>
        </el-table>
      </div>
      <el-pagination
        style="margin-top: 0.1rem"
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="handleCurrentChange"
      >
      </el-pagination>
      <pvWarnDialog ref="dialog"></pvWarnDialog>
    </div>
  </div>
</template>
<script>
import { places } from "@/network/energyTrendAnalysis";
import { pvAlarmQuery } from "@/network/warnMonitor";
import pvWarnDialog from "./pvWarn_dialog.vue";
import { Row } from "element-ui";
export default {
  components: {
    pvWarnDialog,
  },
  data() {
    return {
      data: [],
      defaultProps: {
        children: "children",
        label: "label",
      },
      date1: "",
      date2: "",
      tableData: [],
      arry: {},
      total: 0,
      currentPage: 0,
    };
  },
  mounted() {
    this.date2 = this.date1 =
      this.$getDate().year +
      "-" +
      this.$getDate().month +
      "-" +
      this.$getDate().day;
    this.placesFun();
  },
  methods: {
    /**
     * 获取当前用户的地区企业层级信息
     * **/
    placesFun() {
      places()
        .then((res) => {
          let flag = false;
          res.content.forEach((item) => {
            let idFirst = item.id.substring(0, 1);
            if (idFirst == "A" || idFirst == "C") {
              item["isClick"] = true;
              if (!flag) {
                this.areaId = item.id;
                flag = true;
              }
            } else {
              item["isClick"] = false;
            }
          });
          this.data = this.$arrToTree(res.content);
          this.pvAlarmQueryFun();
        })
        .catch((err) => {});
    },
    /**
     * 点击tree节点
     * **/
    nodeClick(item) {
      if (item.isClick) {
        this.areaId = item.id;
        this.pvAlarmQueryFun();
      }
    },
    /**
     * 光伏告警信息列表
     * **/
    pvAlarmQueryFun() {
      pvAlarmQuery(this.date1, this.areaId, this.currentPage, this.date2)
        .then((res) => {
          const { content, errcode, errmsg } = res;
          if (errcode == 0) {
            this.tableData = content;
          }
        })
        .catch((err) => {});
    },
    /**
     * 分页切换
     * **/
    handleCurrentChange(val) {
      this.currentPage = val;
      this.pvAlarmQueryFun();
    },
    /**
     * 查看详情
     * **/
    handleClick(row) {
      this.$refs.dialog.showDialogFun(row.alarmLine);
    },
  },
};
</script>
<style scoped lang="less">
.energyTrendAnalysis {
  display: flex;
  padding: 0.2rem;
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  position: absolute;
  inset: 0;
}
.border-box {
  border: 1px solid rgba(34, 175, 237, 0.5);
  border-radius: 0.05rem;
  background: rgba(0, 55, 146, 0.2);
  display: flex;
  flex-direction: column;
}
.left {
  width: 25%;
  flex-shrink: 0;
}
.right {
  width: calc(75% - 10px - 0.4rem);
  margin-left: 0.2rem;
  display: flex;
  flex-direction: column;
  padding: 0.2rem 0.2rem;
}
.lineTable {
  display: flex;
  justify-content: space-between;
  flex-grow: 1;
  > div {
    width: 49.5%;
  }
}
.search {
  display: flex;
  > div {
    margin-right: 0.2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: nowrap;
  }
  span {
    color: #00b4fe;
    margin-right: 0.2rem;
    white-space: nowrap;
  }
}
.tabel {
  margin: 0.2rem 0 0;
  flex-grow: 1;
}
.chart {
  flex-grow: 1;
}
/deep/ .el-date-editor.el-input,
/deep/ .el-date-editor.el-input__inner {
  width: 150px;
}
/deep/ .el-input__inner {
  width: 150px;
  background-color: rgba(0, 55, 146, 0.2);
  border: 1px solid rgba(34, 175, 237, 0.5);
}
/**表格**/
/deep/ .el-table,
/deep/ .el-table__expanded-cell,
/deep/ .el-table tr {
  background-color: transparent;
  color: #fff;
}
/deep/ .el-table th.el-table__cell {
  background-color: rgba(0, 90, 228, 0.5);
  color: #fff;
}
/deep/ .el-table td.el-table__cell,
/deep/ .el-table th.el-table__cell.is-leaf {
  border-bottom: none;
  text-align: center;
}
/deep/ .el-table--border::after,
/deep/ .el-table--group::after,
/deep/ .el-table::before {
  background-color: unset;
}
/deep/
  .el-table--striped
  .el-table__body
  tr.el-table__row--striped
  td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
  background-color: rgba(0, 70, 176, 0.5) !important;
}
/deep/
  .el-table--striped
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background: rgba(0, 70, 176, 0.5);
}
/deep/
  .el-table--enable-row-hover
  .el-table__body
  tr:hover
  > td.el-table__cell {
  background-color: unset;
}
.searchBth {
  background: #0066ff;
  color: #fff;
  border-radius: 0.05rem;
  padding: 0 0.2rem;
  border: none;
  cursor: pointer;
  height: 100%;
}
/**树形**/
.el-tree {
  background: #fff0;
  color: #fff;
  padding: 0.2rem;
  overflow: scroll;
}
.tree /deep/ .expanded.el-tree-node__expand-icon {
  color: #fff !important;
}
.tree /deep/ .el-tree-node:focus > .el-tree-node__content,
.tree /deep/ .el-tree-node__content:hover,
.el-upload-list__item:hover {
  background-color: rgba(0, 70, 176, 0.2) !important;
}
/deep/ .el-input__prefix {
  right: 5px;
  left: unset;
}
